nav {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Ensures space between left and right sections */
  width: 100%;
  margin: 0 auto;
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); /* Shadow effect */
  position: fixed; /* Fixes the navbar to the top */
  top: 0; /* Ensures it is at the top of the viewport */
  left: 0;
  z-index: 1000; /* Ensures it appears above other elements */
  height: 63px;
  padding: 0 20px; /* Adds horizontal padding */
}

nav .logo-container {
  display: flex;
  align-items: center; /* Vertically aligns the logo and title */
  text-decoration: none;
}
nav .image-link{
  display: flex;
  align-items: center;
  text-decoration: none;
}
nav .logo {
  max-height: 48px;
  margin-right: 10px; /* Space between logo and title */
  margin-left: 10px;
}

nav .logo-title {
  font-size: 30px; /* Adjust title size as needed */
  font-family: 'Holen Vintage', sans-serif;
  font-weight: 400;
  line-height: 24.51px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color:#54824c;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3); 
}

nav .nav-links {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav .nav-links li a {
  padding: 10px 20px 10px 20px;
  text-transform: uppercase;
  color: rgba(0, 35, 122, 0.5);
  font-family: 'Archivo Narrow', sans-serif;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
}

nav .nav-links li a:not(.contactos-special):hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), inset 0 0 1px rgba(255, 255, 255, 0.6);
  background: rgb(250, 250, 250);
  color: rgba(0, 35, 122, 0.7);
  padding: 20px 20px 20px 20px;
}


nav .contactos-special {
  background-color: #7BA16D;
  color: white!important;
  border-radius: 1000px;
  padding: 7px 15px 7px 15px !important;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  margin-left: 10px;
}

nav .contactos-special:hover{
  background: rgb(250, 250, 250); 
  color: rgba(0, 35, 122, 0.7) !important;
}
@media (max-width: 800px) {
  .burger-menu {
      display: block !important;
  }
}

@media (min-width: 801px) {
  .burger-menu {
      display: none !important;
  }
}
/* Mobile Styles */
@media (max-width: 768px) {
    body{
        width: 100%;
    }
     nav{
        width: 100%;
        margin-right: 0px;
     }
     nav ul {
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style-type: none;
        margin: 0;
        padding: 0;
        flex-grow: 1; /* Ensure the list doesn't stretch beyond its parent */
        flex-wrap: wrap; /* Allow items to wrap to the next line on small screens */
        width:100%
      }
      
     nav ul li {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
      }
      nav ul li a{
        text-align: center;
      }
      .burger-menu {
        display: flex; /* Enable flexbox for centering */
        align-items: center; /* Center the content vertically */
        justify-content: center; /* Center the content horizontally */
        height: 100%; /* Set to 100% of parent height */
        visibility: visible !important;
      }
      
      .burger {
        font-size: 35px; /* Adjust the size of the burger icon */
        background: none;
        border: none;
        color: rgba(85, 85, 85, 0.5); /* 50% opacity */
 /* Match the theme */
        cursor: pointer;
        height: 100%; /* Ensure the button fills the height of the parent */
        display: flex; /* Ensure the content is flexbox for inner centering */
        align-items: center; /* Center the text/icon inside the button */
        justify-content: center; /* Center horizontally inside the button */
        position: relative;
      }
      
      
  
    /* Hide nav links by default */
    #nav-links {
      display: none;
      flex-direction: column;
      background-color: white;
      position: absolute;
      top: 50px;
      width: 100%;
      box-shadow: inset 0 4px 6px -4px rgba(0, 0, 0, 0.3), /* Inset shadow */
                  0px 6px 4px rgba(0, 0, 0, 0.2) !important;
      background-color: #fefefe;
    }
  
    /* Make nav links visible when the 'active' class is added */
    #nav-links.active {
      display: flex;
      position: fixed; /* Set to fixed for overlay effect only when active */
      top: 63px;
      left: 0;
      width: 100%; /* Take full height to cover the screen */
      z-index: 2000; /* Lower than the navbar */
      transform: none; /* Remove if applied */
      filter: none; /* Ensure it overlays other elements */
      border-top: 1px solid #f6f6f6;
      border-bottom: 1px solid #f6f6f6;
      background-color: rgba(245, 245, 245,1);
      box-shadow: inset 0 4px 6px -4px rgba(0, 0, 0, 0.3);
    }
  
    /* Style for burger button */
    
    nav .contactos-special {
      background-color: rgba(245, 245, 245,1) !important;
      color: rgba(0, 35, 122, 0.5) !important;
      box-shadow: none;
      border-radius: 0px;
      padding: 18px;
      height: 41px !important;
      margin-left: 0px !important;
    }
    
    nav .contactos-special:hover{
      background-color: #bcd0b5;
      color: white;
    }
    nav .logo-title {
      font-size: 25px;
    }
    
  }
  